.equals {
  width: 240px;
  padding: 4px;
  box-sizing: border-box;

  margin-top: 8px;

  height: 72px;
}

.equals__operator {

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;

  width: 232px;
  height: 64px;
  left: 80px;
  top: 486px;

  background: #5D5FEF;
  border-radius: 6px;
  border: 0;


  font-weight: 500;
  font-size: 14px;
  line-height: 15px;

  color: #FFFFFF;

  cursor: inherit;

}